@media (max-width: 1199px) {
    .rd-navbar-toggle {
        display: inline-block;
        position: relative;
        width: 50px;
        height: 50px;
        font-size: 24px;
        line-height: 50px;
        text-align: center;
        cursor: pointer;
        color: @color_19;
        background: none;
        border: none;
        outline: none;
        padding: 0;
        span {
            position: relative;
            display: block;
            margin: auto;
            transition: .3s all ease;
            -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
            width: 24px;
            height: 4px;
            background-color: @background_color_42;
            backface-visibility: hidden;
            border-radius: 0;
            &:before {
                -webkit-transform-origin: 1.71429px center;
                -moz-transform-origin: 1.71429px center;
                -ms-transform-origin: 1.71429px center;
                transform-origin: 1.71429px center;
                -webkit-transform-origin: 1.71429px center;
                -moz-transform-origin: 1.71429px center;
                -ms-transform-origin: 1.71429px center;
                transform-origin: 1.71429px center;
                content: "";
                position: absolute;
                left: 0;
                top: -8px;
                transition: .3s all ease;
                width: 24px;
                height: 4px;
                background-color: @background_color_42;
                backface-visibility: hidden;
                border-radius: 0;
            }
            &:after {
                -webkit-transform-origin: 1.71429px center;
                -moz-transform-origin: 1.71429px center;
                -ms-transform-origin: 1.71429px center;
                transform-origin: 1.71429px center;
                -webkit-transform-origin: 1.71429px center;
                -moz-transform-origin: 1.71429px center;
                -ms-transform-origin: 1.71429px center;
                transform-origin: 1.71429px center;
                content: "";
                position: absolute;
                left: 0;
                top: -8px;
                transition: .3s all ease;
                top: 8px;
                width: 24px;
                height: 4px;
                background-color: @background_color_42;
                backface-visibility: hidden;
                border-radius: 0;
            }
        }
    }
    .rd-navbar-toggle.active {
        span {
            -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
            &:before {
                top: 0;
                width: 15px;
                -webkit-transform: rotate3d(0, 0, 1, -40deg);
                transform: rotate3d(0, 0, 1, -40deg);
            }
            &:after {
                top: 0;
                width: 15px;
                -webkit-transform: rotate3d(0, 0, 1, 40deg);
                transform: rotate3d(0, 0, 1, 40deg);
            }
        }
    }
}
